import React from "react";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import { SchoolReducerType } from "../../reducers/school";
import {
  PositionInterface,
  PostRequestStatusInterface,
} from "../../shared/interfaces";
import {
  getPositionById,
  getPostById,
  getSchoolById,
} from "../../shared/dataTest";

interface AppProps {
  positions: PositionInterface[] | null;
  schools: SchoolReducerType;
  selectedRequest: string;
  setModalState: React.Dispatch<
    React.SetStateAction<{
      status: boolean;
      type: "reply" | "detail";
    }>
  >;
  setSelectedRequest: React.Dispatch<React.SetStateAction<string>>;
}

const RequestDetail: React.FC<AppProps> = ({
  positions,
  schools,
  setModalState,
  selectedRequest,
  setSelectedRequest,
}) => {
  const data_post = getPostById(schools.schoolPosts, selectedRequest);
  return (
    <div className="max-w-md mx-auto mb-8">
      <div className="divide-y divide-gray-200">
        <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
          <div className="flex flex-row items-center">
            <span className="mr-2">School:</span>
            <span className="text-xl font-semibold">
              {
                getSchoolById(schools.schoolsInfo, data_post.school_id)
                  .school_title
              }
            </span>
          </div>
          <div className="flex flex-row items-center">
            <span className="mr-2">Position:</span>
            <span className="text-xl font-semibold">
              {positions &&
                getPositionById(positions, data_post.position_id)
                  .position_title}
            </span>
          </div>
          <div className="flex flex-row items-center">
            <span className="mr-2">Qualification:</span>
            <span className="text-xl font-semibold">
              {positions &&
                getPositionById(positions, data_post.position_id)
                  .requirements[0].user_required_docs[0].title}
            </span>
          </div>
          <div className="flex flex-row items-center">
            <span className="mr-2">Posts (requested):</span>
            <span className="text-xl font-semibold">
              {data_post.needed_posts}
            </span>
          </div>
        </div>

        <div className="pt-4 flex justify-between">
          <button
            className="btn bg-gray-400 text-white hover:bg-gray-600"
            onClick={() => {
              setSelectedRequest("");
              setModalState({ status: false, type: "detail" });
            }}
          >
            Exit
          </button>
          {data_post.post_at_school_status ===
            PostRequestStatusInterface.INITIATED && (
            <button
              className="btn bg-primary text-white hover:bg-blue-600"
              onClick={() => setModalState({ status: true, type: "reply" })}
            >
              Reply
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = ({
  schools,
  positionRequests,
}: StoreState): {
  schools: SchoolReducerType;
  positions: PositionInterface[] | null;
} => {
  return { schools: schools, positions: positionRequests };
};

export default connect(mapStateToProps, {})(RequestDetail);
