import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { data_posts_school, data_school } from "../shared/dataTest";
import {
  PostSchoolInterface,
  SchoolInfoInterface,
  SchoolsInterface,
} from "../shared/interfaces";
import { setAxiosToken } from "../utils/AxiosToken";
import axios from "axios";
import { API } from "../utils/api";
import { errorToText } from "../utils/errors";

/**
 * * ****************************** INTERFACES *****************************
 */

export interface GetSchoolsAction {
  type: ActionTypes.GET_SCHOOLS;
  payload: SchoolsInterface[];
}

export interface GetSchoolsInfoAction {
  type: ActionTypes.GET_SCHOOLS_INFO;
  payload: SchoolInfoInterface[];
}

export interface CreateSchoolAction {
  type: ActionTypes.CREATE_SCHOOL;
  payload: SchoolsInterface;
}

export interface UpdateSchoolAction {
  type: ActionTypes.UPDATE_SCHOOL;
  payload: SchoolsInterface;
}

export interface DeleteSchoolAction {
  type: ActionTypes.DELETE_SCHOOL;
  payload: string;
}

// School Posts interfaces
export interface FetchSchoolPostsAction {
  type: ActionTypes.FETCH_SCHOOL_POSTS;
  payload: PostSchoolInterface[];
}

export interface CreateSchoolPostAction {
  type: ActionTypes.CREATE_SCHOOL_POST;
  payload: PostSchoolInterface;
}

export interface RespondRequestInterface {
  post_at_school_id: string;
  approved_posts: number;
  approved_explanation: string;
  approved_date: Date;
  post_at_school_status: number;
  confirmed_by: string;
}
export interface UpdateSchoolPostAction {
  type: ActionTypes.UPDATE_SCHOOL_POST;
  payload: RespondRequestInterface;
}

export interface DeleteSchoolPostAction {
  type: ActionTypes.DELETE_SCHOOL_POST;
  payload: PostSchoolInterface[];
}

export type SchoolActionType =
  | GetSchoolsAction
  | GetSchoolsInfoAction
  | CreateSchoolAction
  | UpdateSchoolAction
  | DeleteSchoolAction
  | FetchSchoolPostsAction
  | CreateSchoolPostAction
  | UpdateSchoolPostAction
  | DeleteSchoolPostAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const getSchools = (callback: Function) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      const res = await axios.get<SchoolsInterface[]>(
        `${API.APPLICATION}/schools`
      );
      dispatch<GetSchoolsAction>({
        type: ActionTypes.GET_SCHOOLS,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.log("Err: ", error);
      callback(false, errorToText(error));
    }
  };
};

export const getSchoolsInfo = () => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();
    // const response = await axios.get<AccessRoleType[]>(url);

    dispatch<GetSchoolsInfoAction>({
      type: ActionTypes.GET_SCHOOLS_INFO,
      payload: data_school,
      // payload: response.data,
    });
  };
};

