import React, { useEffect, useState } from "react";
import { MdClose, MdErrorOutline } from "react-icons/md";
import { connect } from "react-redux";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import Table from "../../components/Table/Table";
import ReplyRequest from "./ReplyRequest";
import RequestDetail from "./RequestDetail";
import { fetchPositions } from "../../actions/positionRequests";
import {
  fetchSchoolPosts,
  updateSchoolPost,
  RespondRequestInterface,
  getSchoolsInfo,
} from "../../actions/schools";
import { StoreState } from "../../reducers";
import { SchoolReducerType } from "../../reducers/school";
import {
  PositionInterface,
  PostRequestStatusInterface,
} from "../../shared/interfaces";
import {
  choosenDistrict,
  displayDate,
  getPositionById,
  getSchoolById,
} from "../../shared/dataTest";

interface AppProps {
  positions: PositionInterface[] | null;
  schools: SchoolReducerType;
  fetchPositions: Function;
  fetchSchoolPosts: Function;
  getSchoolsInfo: Function;
  updateSchoolPost: Function;
}

const _District: React.FC<AppProps> = ({
  positions,
  fetchPositions,
  schools,
  fetchSchoolPosts,
  getSchoolsInfo,
  updateSchoolPost,
}) => {
  const [modalState, setModalState] = useState<{
    status: boolean;
    type: "reply" | "detail";
  }>({ status: false, type: "reply" });
  const [selectedRequest, setSelectedRequest] = useState<string>("");
  const [filterByStatus, setFilterByStatus] = useState<string>("all");
  const [filterBySchool, setFilterBySchool] = useState<string>("all");
  const [filterByPosition, setFilterByPosition] = useState<string>("all");
  const [approvedPosts, setApprovedPosts] = useState<number>(0);
  const [approvedExplanation, setApprovedExplanation] = useState<string>("");
  const [loadind, setLoadind] = useState<boolean>(false);
  const [feedBack, setFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>();

  useEffect(() => {
    if (!positions) {
      fetchPositions();
    }
  }, [positions, fetchPositions]);

  useEffect(() => {
    if (!schools.schoolsInfo.length) {
      getSchoolsInfo();
    }
  }, [schools.schoolsInfo.length, getSchoolsInfo]);

  useEffect(() => {
    if (!schools.schoolPosts.length) {
      fetchSchoolPosts();
    }
  }, [schools.schoolPosts.length, fetchSchoolPosts]);

  const tableData = (): JSX.Element[] => {
    let data = schools.schoolPosts.filter(
      (schoolPost) => schoolPost.district_id === choosenDistrict
    );

    // Filter by position
    data =
      filterByPosition !== "" && filterByPosition !== "all"
        ? data.filter(
            (schoolPost) => schoolPost.position_id === filterByPosition
          )
        : data;
    // Filter by school
    data =
      filterBySchool !== "" && filterBySchool !== "all"
        ? data.filter((schoolPost) => schoolPost.school_id === filterBySchool)
        : data;
    // Filter by status
    // data =data;

    if (data.length === 0) {
      return [
        <tr key={1} className="border-b border-gray-200 hover:bg-gray-100">
          <td className="p-3 text-center" colSpan={7}>
            No Data Found
          </td>
        </tr>,
      ];
    }

    return data.map((schoolPost, index) => (
      <tr
        key={index + 12}
        className="border-b border-gray-200 hover:bg-gray-100"
      >
        <td className="p-3 text-left font-medium">
          {
            getSchoolById(schools.schoolsInfo, schoolPost.school_id)
              ?.school_title
          }
        </td>
        <td className="p-3 text-left font-medium">
          {positions &&
            getPositionById(positions, schoolPost.position_id)?.position_title}
        </td>
        <td className="p-3 text-center text-base font-medium">
          {schoolPost.needed_posts}
        </td>
        <td className="p-3 text-center text-base font-medium">
          {schoolPost.confirmed_posts ? schoolPost.confirmed_posts : "-"}
        </td>
        <td className="p-3 text-center text-base font-medium">
          {displayDate(schoolPost.request_date)}
        </td>
        <td
          className={`p-3 text-center text-base font-medium text-${
            schoolPost.post_at_school_status ===
            PostRequestStatusInterface.INITIATED
              ? "yellow"
              : "green"
          }-700`}
        >
          <span>
            {schoolPost.post_at_school_status ===
            PostRequestStatusInterface.INITIATED
              ? "Pending"
              : "Responded"}
          </span>
        </td>
        <td className="text-left">
          <div className="flex item-center justify-center">
            {schoolPost.post_at_school_status ===
              PostRequestStatusInterface.INITIATED && (
              <button
                className="
                btn
                text-gray-700
                ml-2
                border-gray-700
                md:border-2
                hover:bg-gray-700
                hover:text-white
                transition
                ease-out
                duration-500
              "
                title="Reply to Request"
                onClick={() => {
                  setModalState({ status: true, type: "reply" });
                  setSelectedRequest(schoolPost.post_at_school_id);
                }}
                disabled={modalState && modalState.status}
              >
                Reply
              </button>
            )}
            <button
              className="
                btn
                text-blue-700
                ml-2
                border-blue-700
                md:border-2
                hover:bg-blue-700
                hover:text-white
                transition
                ease-out
                duration-500
              "
              title="View User Profile"
              onClick={() => {
                setModalState({ status: true, type: "detail" });
                setSelectedRequest(schoolPost.post_at_school_id);
              }}
              disabled={modalState && modalState.status}
            >
              Details
            </button>
          </div>
        </td>
      </tr>
    ));
  };

  const submitResponse = (e: React.FormEvent<HTMLFormElement>): void => {
    e.preventDefault();
    setFeedBack({ type: "", message: "" });

    setLoadind(true);
    console.log({ approvedPosts, approvedExplanation });
    const dataForm: RespondRequestInterface = {
      post_at_school_id: selectedRequest,
      approved_posts: approvedPosts,
      approved_explanation: approvedExplanation,
      approved_date: new Date(),
      post_at_school_status: 1,
      confirmed_by: "432232",
    };

    updateSchoolPost(dataForm);
    setSelectedRequest("");
    setModalState({ status: false, type: "reply" });
    setFeedBack({ type: "success", message: `Posts has been requested!` });
    setLoadind(false);
  };

  return (
    <div className="mx-32">
      {/* Title */}
      <div className="bg-hero my-5 rounded p-4">
        <div className="flex justify-between items-center relative">
          <div className="flex flex-col">
            <span className="text-white text-2xl">Nyarugenge District</span>
            <span className="text-gray-200 font-light text-sm mt-1">
              <span className="font-bold">--</span>
            </span>
          </div>
          {/* <div className="text-white absolute top-0 right-0 mt-2">
            <nav className="flex flex-col sm:flex-row mx-1">
              <label htmlFor="academic-period" className="m-2">
                Academic Period
              </label>
              <select
                className="border border-black bg-white text-gray-800 p-1 rounded-10"
                id="academic-period"
              >
                <option>2020-2021</option>
                <option>2020-2021</option>
                <option>2020-2021</option>
              </select>
            </nav>
          </div> */}
        </div>

        {/* <div className="grid grid-cols-3 lg:grid-cols-3 gap-5 w-full p-4">
          <div className="bg-gray-600 py-2 rounded-md text-green-500">
            <div className="flex relative">
              <div className="flex flex-col relative z-10">
                <h2 className="text-base md:text-2xl lg:text-5xl px-2 whitespace-no-wrap">
                  Vacant Post
                </h2>
                <div className="flex w-full absolute bottom-1">
                  <h2 className="ltext-base md:text-2xl lg:text-5xl px-4 py-2 lg:px-8 text-gray-600">
                    80
                  </h2>
                  <div className="flex flex-col">
                    <span className="">Schools:8</span>
                    <span className="">Positions:8</span>
                  </div>
                </div>
              </div>
              <div className="z-0">
                <svg
                  className="w-10 h-10 md:w-20 md:h-20 lg:w-36 lg:h-36 text-gray-400 "
                  fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg"
                  viewBox="0 0 640 512"
                >
                  <path d="M96 224c35.3 0 64-28.7 64-64s-28.7-64-64-64-64 28.7-64 64 28.7 64 64 64zm448 0c35.3 0 64-28.7 64-64s-28.7-64-64-64-64 28.7-64 64 28.7 64 64 64zm32 32h-64c-17.6 0-33.5 7.1-45.1 18.6 40.3 22.1 68.9 62 75.1 109.4h66c17.7 0 32-14.3 32-32v-32c0-35.3-28.7-64-64-64zm-256 0c61.9 0 112-50.1 112-112S381.9 32 320 32 208 82.1 208 144s50.1 112 112 112zm76.8 32h-8.3c-20.8 10-43.9 16-68.5 16s-47.6-6-68.5-16h-8.3C179.6 288 128 339.6 128 403.2V432c0 26.5 21.5 48 48 48h288c26.5 0 48-21.5 48-48v-28.8c0-63.6-51.6-115.2-115.2-115.2zm-223.7-13.4C161.5 263.1 145.6 256 128 256H64c-35.3 0-64 28.7-64 64v32c0 17.7 14.3 32 32 32h65.9c6.3-47.4 34.9-87.3 75.2-109.4z"></path>
                </svg>
              </div>
            </div>
          </div>
        </div> */}
      </div>

      <div className="bg-white shadow-md p-2">
        {feedBack && feedBack.type !== "" && (
          <div
            className={`alert-${feedBack.type} flex flex-row items-center relative`}
          >
            <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
              <MdClose
                className="h-5 w-5 text-gray-700 hover:font-bold"
                title="Close"
                onClick={() => setFeedBack({ type: "", message: "" })}
              />
            </span>
            <MdErrorOutline className="h-10 w-10" />
            <div className="alert-content ml-4">
              <div className="alert-title font-semibold text-lg">
                {feedBack.type === "success" ? "Success" : "Error"}
              </div>
              <div className="alert-description text-sm">
                {feedBack.message}
              </div>
            </div>
          </div>
        )}
        <div className="grid md:grid-cols-5 gap-2">
          <div className={`md:col-span-5 rounded`}>
            <div className="my-2 flex sm:flex-row flex-col justify-between">
              <div className="flex flex-col md:flex-row">
                <div className="mr-2">
                  <select
                    className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none"
                    onChange={(e) => setFilterByStatus(e.target.value)}
                  >
                    <option value="all">All</option>
                    <option value="0">Pending</option>
                    <option value="1">Responded</option>
                  </select>
                </div>
                <div className="mr-2">
                  <select
                    className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none"
                    onChange={(e) => setFilterBySchool(e.target.value)}
                  >
                    <option value="all">All School</option>
                    {schools.schoolsInfo
                      .filter((s) => s.district_id === choosenDistrict)
                      .map((school, index) => (
                        <option key={index + 1} value={school.school_id}>
                          {school.school_title}
                        </option>
                      ))}
                  </select>
                </div>
                <div className="mr-2">
                  <select
                    className="border-2 border-gray-700 bg-white p-2 rounded focus:outline-none"
                    onChange={(e) => setFilterByPosition(e.target.value)}
                  >
                    <option value="all">All Position</option>
                    {positions && positions.length > 0 ? (
                      positions.map((position, index) => (
                        <option key={index + 1} value={position.position_id}>
                          {position.position_title}
                        </option>
                      ))
                    ) : (
                      <option value="">No position found</option>
                    )}
                  </select>
                </div>
              </div>
              <div className="block relative">
                {/* <div className="pt-6 md:pt-0 md:pl-6">
                  <button
                    className="btn bg-primary text-white hover:bg-blue-600"
                    onClick={() => setReplyRequest(true)}
                    disabled={replyRequest}
                  >
                    Request Teacher
                  </button>
                </div> */}
              </div>
            </div>
            <Table
              columns={[
                { columnTitle: "SCHOOL", alignText: "left", padding: "p-3" },
                { columnTitle: "POSITION", alignText: "left", padding: "p-3" },
                { columnTitle: "REQUEST", alignText: "center", padding: "p-3" },
                {
                  columnTitle: "APPROVED",
                  alignText: "center",
                  padding: "p-3",
                },
                {
                  columnTitle: "REQUEST DATE",
                  alignText: "center",
                  padding: "p-3",
                },
                { columnTitle: "STATUS", alignText: "center", padding: "p-3" },
                { columnTitle: "", alignText: "left", padding: "p-3" },
              ]}
              rows={tableData}
              pagination={true}
              search={true}
            />
          </div>
        </div>
      </div>

      {/* Upload Modal */}
      {modalState && modalState.status && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {}}
          displayClose={false}
          backDropClose={false}
          title={
            <div className="flex items-center space-x-5">
              <div className="h-14 w-14 bg-blue-200 rounded-full flex flex-shrink-0 justify-center items-center text-blue-500 text-2xl font-mono">
                R
              </div>
              <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                <p className="text-base text-gray-500 font-medium">
                  You are applying for
                </p>
                <h2 className="uppercase text-blue-600">
                  Reply to Post Request
                </h2>
              </div>
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          {modalState.type === "reply" ? (
            <ReplyRequest
              approvedPosts={approvedPosts}
              setApprovedPosts={setApprovedPosts}
              approvedExplanation={approvedExplanation}
              setApprovedExplanation={setApprovedExplanation}
              setModalState={setModalState}
              submitResponse={submitResponse}
              loadind={loadind}
              selectedRequest={selectedRequest}
              setSelectedRequest={setSelectedRequest}
            />
          ) : (
            <RequestDetail
              setModalState={setModalState}
              selectedRequest={selectedRequest}
              setSelectedRequest={setSelectedRequest}
            />
          )}
        </Modal>
      )}
      {/* End of Upload Modal */}
    </div>
  );
};

const mapStateToProps = ({
  schools,
  positionRequests,
}: StoreState): {
  schools: SchoolReducerType;
  positions: PositionInterface[] | null;
} => {
  return { schools: schools, positions: positionRequests };
};

export const District = connect(mapStateToProps, {
  fetchPositions,
  getSchoolsInfo,
  fetchSchoolPosts,
  updateSchoolPost,
})(_District);
