import React from "react";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import { SchoolReducerType } from "../../reducers/school";
import { PositionInterface } from "../../shared/interfaces";
import {
  displayDate,
  getPositionById,
  getPostById,
  getSchoolById,
} from "../../shared/dataTest";

interface AppProps {
  positions: PositionInterface[] | null;
  schools: SchoolReducerType;
  approvedPosts: number;
  setApprovedPosts: React.Dispatch<React.SetStateAction<number>>;
  approvedExplanation: string;
  setApprovedExplanation: React.Dispatch<React.SetStateAction<string>>;
  submitResponse: Function;
  setModalState: React.Dispatch<
    React.SetStateAction<{
      status: boolean;
      type: "reply" | "detail";
    }>
  >;
  loadind: boolean;
  selectedRequest: string;
  setSelectedRequest: React.Dispatch<React.SetStateAction<string>>;
}

const ReplyRequest: React.FC<AppProps> = ({
  positions,
  schools,
  approvedPosts,
  setApprovedPosts,
  approvedExplanation,
  setApprovedExplanation,
  submitResponse,
  setModalState,
  loadind,
  selectedRequest,
  setSelectedRequest,
}) => {
  const data_post = getPostById(schools.schoolPosts, selectedRequest);
  return (
    <div className="max-w-md mx-auto mb-8">
      <form onSubmit={(e) => submitResponse(e)}>
        {console.log(data_post)}
        <div className="divide-y divide-gray-200">
          <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
            <div className="flex flex-row items-center">
              <span className="mr-2">School:</span>
              <span className="text-xl font-semibold">
                {
                  getSchoolById(schools.schoolsInfo, data_post.school_id)
                    .school_title
                }
              </span>
            </div>
            <div className="flex flex-row items-center">
              <span className="mr-2">Position:</span>
              <span className="text-xl font-semibold">
                {positions &&
                  getPositionById(positions, data_post.position_id)
                    .position_title}
              </span>
            </div>
            <div className="flex flex-row items-center">
              <span className="mr-2">Qualification:</span>
              <span className="text-xl font-semibold">
                {positions &&
                  getPositionById(positions, data_post.position_id)
                    .requirements[0].user_required_docs[0].title}
              </span>
            </div>
            <div className="flex flex-row items-center">
              <span className="mr-2">Requested Date:</span>
              <span className="text-xl font-semibold">
                {displayDate(data_post.request_date)}
              </span>
            </div>
            <div className="flex flex-row items-center">
              <span className="mr-2">Posts (requested):</span>
              <span className="text-xl font-semibold">
                {data_post.needed_posts}
              </span>
            </div>
            <div className="flex flex-col">
              <label>Posts (approved)</label>
              <input
                type="number"
                className="input input-border"
                min={0}
                max={data_post.needed_posts}
                placeholder="1"
                value={approvedPosts}
                onChange={(e) => {
                  setApprovedPosts(Number(e.target.value));
                }}
                required
              />
            </div>
            {/* <div className="flex items-center space-x-3">
                    <div className="flex flex-col">
                      <label className="leading-loose">Post (approved)</label>
                      <input
                        type="number"
                        className="input input-border"
                        placeholder="1"
                        size={3}
                        min={0}
                      />
                    </div>
                    <div className="flex flex-row items-center">
                      <span className="mr-2">Requested:</span>
                      <span className="text-xl font-semibold">3</span>
                    </div>
                  </div> */}
            <div className="flex flex-col">
              <label>Explanation</label>
              <textarea
                name="description"
                className="input input-border"
                id="description"
                placeholder="Explanation"
                rows={2}
                onChange={(e) => {
                  setApprovedExplanation(e.target.value);
                }}
                value={approvedExplanation}
                required
              ></textarea>
            </div>
          </div>
          <div className="pt-4 flex justify-between">
            <button
              className="btn bg-gray-400 text-white hover:bg-gray-600"
              onClick={() => {
                setModalState({ status: false, type: "reply" });
                setSelectedRequest("");
              }}
              disabled={loadind}
            >
              Exit
            </button>
            <button
              className="btn bg-primary text-white hover:bg-blue-600"
              disabled={loadind}
            >
              Request
            </button>
          </div>
        </div>
      </form>
    </div>
  );
};

const mapStateToProps = ({
  schools,
  positionRequests,
}: StoreState): {
  schools: SchoolReducerType;
  positions: PositionInterface[] | null;
} => {
  return { schools: schools, positions: positionRequests };
};

export default connect(mapStateToProps, {})(ReplyRequest);
